

import { motion } from 'framer-motion';
 
const chips = [
  { label: "UI UX DESIGN", color: "#2633C5" },
  { label: "Wireframe", color: "#7D4CDB" },
  { label: "App Design", color: "#F79487" },
  { label: "CONSULTING", color: "#1C1C1C" },
  { label: "Website Design", color: "#FBD460" },
  { label: "Research", color: "#8DD9A5" },
];
 
const containerVariants = {
  hidden: {},
  show: {
    transition: {
      staggerChildren: 0.2,
    },
  },
};
 
const chipVariants = {
  hidden: { y: -100, opacity: 0, rotate: -10 },
  show: {
    y: 0,
    opacity: 1,
    rotate: 0,
    transition: {
      type: 'spring',
      stiffness: 100,
      damping: 10,
    },
  },
};
 
export default function FallingChips() {
  return (
<motion.div
      className="flex flex-wrap gap-4 justify-center mt-20"
      variants={containerVariants}
      initial="hidden"
      animate="show"
>
      {chips.map((chip, index) => (
<motion.div
          key={index}
          variants={chipVariants}
          style={{
            background: chip.color,
            color: chip.color === '#1C1C1C' ? 'white' : 'black',
            padding: '10px 20px',
            borderRadius: '30px',
            fontWeight: 'bold',
            fontSize: '14px',
          }}
>
          {chip.label}
</motion.div>
      ))}
</motion.div>
  );
}